import React, { useState, useEffect } from 'react';
import { ScrollDirectionButtonContainer } from './styles';

const ScrollDirectionButton = () => {
  const [buttonDirection, setButtonDirection] = useState('down');

  useEffect(() => {
    const handleScroll = () => {
        const buttonContainer = document.querySelector('.button-container');
        const scrollButton = document.querySelector('.scroll-direction-button');
      
        if (buttonContainer && scrollButton) {
          const buttonContainerRect = buttonContainer.getBoundingClientRect();
          const scrollButtonRect = scrollButton.getBoundingClientRect();
      
          const verticalDistance = Math.abs(buttonContainerRect.top - scrollButtonRect.top);
      
          if (verticalDistance < 150) {
            setButtonDirection(buttonContainerRect.left > scrollButtonRect.left ? 'right' : 'left');
          } else if (buttonContainerRect.top > scrollButtonRect.top) {
            setButtonDirection('down');
          } else {
            setButtonDirection('up');
          }
        }
      };
  
    window.addEventListener('scroll', handleScroll);
  
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const scrollTo = () => {
    const nameInput = document.querySelector('input[name="name"]');
    if (nameInput) {
      const nameInputRect = nameInput.getBoundingClientRect();
      window.scrollTo({
        top: nameInputRect.top + window.scrollY - 100,
        behavior: 'smooth',
      });
    }
  };

  return (
    <ScrollDirectionButtonContainer 
      className={`scroll-direction-button ${buttonDirection}`} 
      onClick={scrollTo}
    />
  );
};

export default ScrollDirectionButton;
