import styled, { keyframes } from "styled-components";

const neonGlow = keyframes`
  0% {
    box-shadow: 0 0 2px #286fff, 0 0 4px #286fff, 0 0 6px #286fff, 0 0 8px #7aa9ff, 0 0 10px #7aa9ff, 0 0 11px #7aa9ff, 0 0 12px #7aa9ff;
  }
  50% {
    box-shadow: 0 0 4px #286fff, 0 0 6px #286fff, 0 0 8px #286fff, 0 0 9px #7aa9ff, 0 0 10px #7aa9ff, 0 0 11px #7aa9ff, 0 0 12px #7aa9ff;
  }
  100% {
    box-shadow: 0 0 2px #286fff, 0 0 4px #286fff, 0 0 6px #286fff, 0 0 8px #7aa9ff, 0 0 10px #7aa9ff, 0 0 11px #7aa9ff, 0 0 12px #7aa9ff;
  }
`;

const casinoBounceVertical = keyframes`
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
`;

const casinoBounceHorizontal = keyframes`
  0%, 100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-10px);
  }
`;

export const ScrollDirectionButtonContainer = styled("div")`
  width: clamp(70px, 5vmin, 60px);
  height: clamp(70px, 5vmin, 60px);
  max-width: 70px;
  max-height: 70px;
  position: fixed;
  right: 2vw;
  bottom: 26vh;
  z-index: 100;
  cursor: pointer;
  border-radius: 4px;
  transition: all 0.3s ease-in-out;
  display: flex;
  animation: ${neonGlow} 2s infinite alternate;

  @media (max-width: 768px) {
    right: 4vw;
    bottom: 16vh;
    width: clamp(40px, 4vmin, 50px);
    height: clamp(40px, 4vmin, 50px);
  }

  &.down, &.up {
    animation: ${casinoBounceVertical} 1s infinite, ${neonGlow} 2s infinite alternate;
    background-image: url('/img/svg/down-arrow.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &.up {
    background-image: url('/img/svg/up-arrow.svg');
  }

  &.left, &.right {
    animation: ${casinoBounceHorizontal} 1s infinite, ${neonGlow} 2s infinite alternate;
    background-image: url('/img/svg/left-arrow.svg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &.right {
    background-image: url('/img/svg/right-arrow.svg');
  }
`;
